.bigcard-container {
    display: flex;
    align-items: center;
    border-top: 1px solid rgba(0, 0, 0, 0.274);
    
    padding: 20px 10px;
    margin-bottom: 10px;
    height: 200px;
    
}



.bigcard-container > img {
    width: 70px;
    margin-right: 10px;
    border-radius: 50%;
}

.bigcard-container h4 {
    margin-bottom: 15px;
}

.bigcard-container > div {
    display: flex;
    flex-direction: column;
    justify-items: flex-start;
} 